<template>
  <div class="cx_box">
      <div class="cx_box-content">
          <!-- <div><input type="file" id='imageA' style="display:none" accept="image/*" capture='camera' @change="change"></div> -->
          <div class="zfaceRecord">
              <div class="zfaceRecord-video">
                  <video width="375px" height="500px" ref="video" controls allowsInlineMediaPlayback="true"></video>
                  <!-- <video style="transform: scale(-100%, 100%);" ref="video"></video> -->
              </div>
              <div class="zfaceRecord-opat">
                  <div class="zfaceRecord-recordData">
                      <video style="display:none;" loop height="100%" ref="Camera" controls allowsInlineMediaPlayback="true"> </video>
                  </div>
                  <!-- <div class="zfaceRecord-head">

                  </div> -->

                  <div class="zfaceRecord-btn" v-if="this.videoWidth && this.videoHeight">
                      <p class="zfaceRecord-record"  v-if="isRecord == 0" @click="startRecord"></p>
                      <p class="zfaceRecord-complete" v-else-if="isRecord == 1" @click="stopRecord"></p>
                      <p class="zfaceRecord-submit" v-else-if="isRecord == 2"  @click="submitRecord"></p>
                  </div>
                  <div class="zfaceRecord-reset" ref="reset" v-if="isRecord == 2" @click="resetRecord"></div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
  export default {
      props: {},
      data () {
          return {
              isSupport: true, // 是否支持视频采集
              mediaStreamTrack: {},
              video_stream: '', // 视频stream
              camera: '', // 采集后的视频
              video: '', // 采集中的视频
              buffer: [], // 录制视频buffer
              clientWidth: 0,
              clientHeight: 0,
              videoWidth: 0,
              videoHeight: 0,
              mediaRecorder: null, // 录制视频对象
              isRecord: 0, // 是否已按快门
              constraints: null, // 设置约束配置
              facePath: '', // 人脸路径
              from: 0, // 来源 0-首次认证 1-项目 2-培训班 3-项目需求调研 4-培训班需求调研 5-课程 6-课程包 7-培训课 8-直播
              source: '', // 来源明细
              similarityScore: 0, // 相似度
              terminal: null, // 终端(默认0) 0-移动端 1-PC端
              enc: '', // 安全校验enc
              time: '', // 安全校验time
              identifyType: '' // 2-PC扫码人脸识别 3-登录跳转人脸识别
          }
      },
      mounted () {
          this.init()
          // 进入页面 自动调用摄像头
          this.getCamera()
      },
      methods: {
          /**
           * 初始化
           */
          init () {
              this.camera = this.$refs.Camera
              this.video = this.$refs.video
          },
          resetRecord () {
              this.isRecord = 0
              this.camera.style.display = 'none'
              this.video.style.display = 'block'
              // this.$refs.video.removeEventListener('ended')
          },
         /**
          * 获取视频流媒体
          */
         gotMediaStream (stream) {
              // 摄像头开启成功
              this.mediaStreamTrack = typeof stream.stop === 'function' ? stream : stream.getTracks()[0]
              this.video_stream = stream

              this.video.srcObject = stream
              this.video.setAttribute('loop', true)
              this.video.play()
              return navigator.mediaDevices.enumerateDevices()
          },
          /**
           * 获取窗口和video预成像尺寸
           */
          gotMediaSize () {
              this.clientWidth = document.body.clientWidth // 初始化窗口尺寸
              this.clientHeight = document.body.clientHeight
              this.videoWidth = this.$refs.video.clientWidth // 初始化video尺寸
              this.videoHeight = this.$refs.video.clientHeight
          },
          handleError () {

          },
          handleDataAvailable (e) {
              if (e && e.data && e.data.size > 0) {
                  this.buffer.push(e.data)
              }
          },
          startRecord () {
              this.buffer = []
              this.camera.style.display = 'none'
              var options = {
                  mimeType: 'video/webm;codecs=vp8'
              }
              if (!MediaRecorder.isTypeSupported(options.mimeType)) {
                  console.log(`${options.mimeType} is not supported`)
              }

              try {
                  this.mediaRecorder = new MediaRecorder(this.video_stream, options)
                  this.isRecord = 1
              } catch (e) {
                  console.error('Failed to create MediaRecorder:', e)
                  return
              }

              this.mediaRecorder.ondataavailable = this.handleDataAvailable
              this.mediaRecorder.start(10)
          },
          /**
           * 停止录制视频
           */
          stopRecord () {
              this.isRecord = 2
              this.mediaRecorder.stop()
              this.setVideo()
          },
          /**
           * 提交视频
           */
          submitRecord () {
              if (this.isRecord == 2) {
                  this.isRecord = 3
                  this.$store.commit('SET_LOAD_ALIVE', true)
                  var blob = new Blob(this.buffer, {type: 'video/webm'})
                  // this.blobToBase64(blob, (o) => {
                      //     this.facelivenessVerify(o)
                  // })
                  var file = new File([blob], 'myvideo', {type: 'video/webm'})
              }
          },
          // 调用打开摄像头功能
          getCamera () {
              // 旧版本浏览器可能根本不支持mediaDevices，我们首先设置一个空对象
              if (!navigator.mediaDevices) {
                  this.isSupport = false
                  navigator.mediaDevices = {}
                  return
              }
              this.constraints = {
                  'video': true,
                  'audio': false
              }

              // 正常支持版本
              navigator.mediaDevices
                  .getUserMedia(this.constraints)
                  .then(this.gotMediaStream)
                  .then(this.gotMediaSize)
                  .catch((err) => {
                      console.log(err)
                  })
          },
          // 将录制视频缓存到video
          setVideo () {
              this.gotMediaSize()
              var blob = new Blob(this.buffer, {type: 'video/webm'})
              this.camera.style.display = 'block'
              this.video.style.display = 'none'
              this.camera.src = window.URL.createObjectURL(blob)
              this.camera.srcObject = null
              this.camera.play()
          },
          // 打开摄像头
          OpenCamera () {
              this.getCamera()
          },
          // 关闭摄像头
          CloseCamera () {
              console.log('关闭摄像头')
              this.video.srcObject.getTracks()[0].stop()
          }
          /**
           * 获取文件的Base64
           * @param file      {File}      文件
           * @param callback  {Function}  回调函数，参数为获取到的base64
           */
          // blobToBase64 (blob, callback) {
          //     const reader = new FileReader()
          //     reader.readAsDataURL(blob)
          //     reader.onload = (ev) => {
          //         var videoBase64 = ev.target.result
          //         callback(videoBase64)
          //     }
          // }
      }
  }
</script>

<style>
.cx_box {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    height: 100vh;
}
.cx_box-content {
    -webkit-box-flex: 1;
    overflow: hidden;
    overflow-y: auto;
}
.zfaceRecord { height: 100%; width: 100%; background: #f5f6f8; margin: 0; padding: 0;}
.zfaceRecord-recordData,
.zfaceRecord-video {
  position: fixed; left: 0; top: 0;
  width: 100%;
  height: 100%;
  display: flex; justify-content: center;
  overflow: hidden;
  transform: scaleX(-1);
}
.zfaceRecord-video video { height: 100%;}
.zfaceRecord-camera,
.zfaceRecord-opat { position: fixed; left: 0; top: 0; width: 100%; height: 100%;}
.zfaceRecord-camera img {
  width: 100%;
  height: 100%;
}
  .zfaceRecord-btn { position: absolute; z-index: 1000; bottom: .2rem; left: 50%; margin-left: -.7rem;}
   .zfaceRecord-submit,
.zfaceRecord-complete,
.zfaceRecord-record { position: relative; display: flex; align-items: center; justify-content: center; width: 1rem; height: 1rem; padding: 0.12rem; border: .05rem #ccc solid; border-radius: 50%; background: #ccc;}
.zfaceRecord-record::after { display: block; content: ''; width: 100%; height: 100%; border-radius: 50%; background: #f5f6f8;}
.zfaceRecord-record::before { position: absolute; left: 50%; top: 50%;; margin-left: -15%; margin-top: -15%; display: block; content: ''; width: 30%; height: 30%; border-radius: 50%; background: #f00;}
.zfaceRecord-complete { background: none; border-color: #fff;}
.zfaceRecord-complete::after { display: block; content: ''; width: 50%; height: 50%; border-radius: 12%; background: #fff;}
.zfaceRecord-complete::before { display: none;}
.zfaceRecord-submit { border-color: transparent; background-color: #fff;}
.zfaceRecord-submit::after { display: block; content: ''; width: 100%; height: 100%; border-radius: 50%; background: red; background-size: 100%; background-color: #fff;}
.zfaceRecord-reset { position: absolute; top: .45rem; right: .45rem; width: .56rem; height: .56rem; background: green; background-size: 100%;}
</style>
